<template>
    <div class="music">
      <a-row>
        <a-col :span="18">
           <SubNavBar :items="subNavBarItems" @navChange="navChangeHandler" class="sub-navbar" />
          <div class="music-list-container">
            <AudioPlayer
              v-for="(musicObject,index) in getCurrentPageMusic(current)"
              :key="index"
              :musicObject="musicObject"
              :index="index"
              @popWindow="popWindowHandler"
            />
          </div>
          <a-pagination class="pagination" v-model="current" :total="currentTotalMusicAmount" />
        </a-col>
        <a-col :span="6">
          <ContactCard class="contact-card" />
          <LicenseCard class="license-card" />
        </a-col>
      </a-row>
      <MusicPopWindow
        v-if="showMusicPopWindow"
        :title="popWindowInfo.title"
        :artist="popWindowInfo.artist"
        :songPath="popWindowInfo.songPath"
        @onClose="showMusicPopWindow = false"
        class="pop-window"
      />
    </div>
</template>

<script>
import AudioPlayer from "../components/AudioPlayer";
import ContactCard from "../components/ContactCard";
import MusicPopWindow from "../components/MusicPopWindow";
import songs from "../assets/music.json";
import SubNavBar from "../components/SubNavBar";
import LicenseCard from "../components/LicenseCard"

export default {
  data() {
    return {
      currentMusic: songs,
      current: 1,
      showMusicPopWindow: false,
      popWindowInfo: {},
      subNavBarItems: [
        { key: "all", text: "全部" },
        { key: "atmosphere", text: "气氛"},
        { key: "romantic", text: "浪漫" },
        { key: "epic", text: "史诗" },
        { key: "drama", text: "戏剧" },
        { key: "other", text: "其他" }
      ],
      genreKeys: {
        all: "全部",
        atmosphere: "气氛",
        romantic: "浪漫",
        epic: "史诗",
        drama: "戏剧",
        percussion: "打击乐",
        other: ["恐怖", "世界", "杂项", "电子", "乐观"]
      },
    };
  },
  components: {
    AudioPlayer,
    ContactCard,
    MusicPopWindow,
    SubNavBar,
    LicenseCard
  },
  computed: {
    currentTotalMusicAmount() {
      return this.currentMusic.length;
    }
  },
  methods: {
    getCurrentPageMusic(pageNum) {
      pageNum = pageNum - 1;
      const start = pageNum * 10;
      const result = this.currentMusic.slice(start, start + 10);
      return result;
    },
    popWindowHandler(musicObject) {
      this.popWindowInfo.title = musicObject.name || musicObject.title;
      this.popWindowInfo.artist = musicObject.artist;
      let songPath;
      if (musicObject.name) {
        songPath = `./music/${musicObject.title.toUpperCase()}.mp3`;
      } else {
        songPath = `./music/${musicObject.title}.mp3`;
      }
      this.popWindowInfo.songPath = songPath;
      this.showMusicPopWindow = true;
    },
    navChangeHandler(value) {
      let newMusic;
      if(value === "all") {
        newMusic = songs;
      }
      else if (value === "other") {
        newMusic = songs.filter(song => {
          return this.genreKeys.other.includes(song.genre);
        });
      } else {
        const genreKey = this.genreKeys[value];
        newMusic = songs.filter(song => {
          return song.genre === genreKey;
        });
      }
      this.currentMusic = newMusic;
    }
  }
};
</script>

