<template>
  <div class="audio-player" v-bind:class="{iseven: isEven}">
    <p class="song-title">{{musicObject.name || musicObject.title}}</p>
    <p>{{musicObject.artist}}</p>
    <div class="play-button"><a-button type="primary" @click="playHandler">{{buttonText}}</a-button></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      buttonText: "播放"
    }
  },

  props:[
    "musicObject",
    "index"
  ],

  computed: {
    isEven() {
      return this.index % 2 == 0
    }
  },

  methods: {
   playHandler(e) {
     this.$emit('popWindow', this.musicObject)
   } 
  }

}
</script>

