<template>
  <div class="animation-card">
    <h3>{{object.title}}</h3>
    <div class="gif-container">
      <img  v-for="(path, index) in imgPaths" :key=index :src=path alt="">
    </div>
  </div>
</template>

<script>
export default {
  name: 'AnimationCard',
  props:[
    "object"
  ],
  data() {
    return {
      gifNumber: this.object.gifNumber,
      imgPaths:[]
    }
  },
  mounted: function(){
    for(let i = 0; i < this.gifNumber; i++) {
      const path = `./gif/${this.object.title}/${i}.gif`;
      this.imgPaths.push(path);
    }
  }

}
</script>

<style scoped>

.gif-container{
  display:flex;
  flex-wrap: wrap;
  width:800px;

}

.gif-container img{
  width:100px;
  height:100px;
  margin:20px;
  border-radius:10px;
}

h3 {
  color: #6e6e6e;
  margin-top:1em;
}


</style>