import Vue from 'vue'
import App from './App.vue'
import router from './router.js'
import AV from 'leancloud-storage'
import { appId, appKey } from '../config'
import "./plugins/ant-design-vue"

Vue.config.productionTip = true;

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')

AV.init({
  appId,
  appKey
})

