<template>
<div class="cast-card" @click="clickHandler">
  <img :src="thumbnailPath" alt="thumbnail">
  <div class="banner">
    <p>{{castObject.name}}</p>
    <p>在线： {{randomAudienceNumber(castObject.audience)}}</p>
  </div>
</div>
  
</template>

<script>
export default {
  props:[
    "castObject",
    "roomId",
  ],
  computed:{
      thumbnailPath() {
        return `./cast_thumbnail/${this.castObject.id}.jpg`
      }
    },
  methods: {
    clickHandler() {
      let newWindow = this.$router.resolve({ name: 'room', params: { id: this.castObject.id}})
      window.open(newWindow.href, '_blank');
    },
    randomAudienceNumber(num) {
      return num + Math.floor(Math.random() * 5)
    }
  }
}
</script>

