<script>
import ChatArea from "../components/ChatArea";
import hostIdMap from "../assets/host_id_map.json";
import hostAudienceMap from "../assets/host_audience_map.json"
export default {
  data() {
    return {
      showAlert: false
    };
  },
  components: {
    ChatArea
  },
  computed: {
    videoPath() {
      if(13 - this.$route.params.id == 6) {
        return `http://certification.blinnnk.com/13.mp4`
      } else if(13 - this.$route.params.id == 8) {
        return `http://certification.blinnnk.com/14.mp4`
      }
      return `http://certification.blinnnk.com/${13 - this.$route.params.id}.mp4`
    },
    avatarPath() {
      return `./cast_thumbnail/${this.$route.params.id}.jpg`;
    },
    crownPath() {
      return `./素材/皇冠/${this.$route.params.id}.png`;
    },
    levelPath() {
      return `./素材/等级/${this.$route.params.id}.png`;
    },
    hostName() {
      return hostIdMap[this.$route.params.id];
    },
    audience() {
      return hostAudienceMap[this.$route.params.id]
    }
  },
  methods: {
    sendButtonClickHandler() {
      this.showAlert = true;
    }
  }
};
</script>

