<template>
  <div class="chat-area">
    <div class="screen">
      <p
        class="message"
        :style="{ color: randomColor }"
        v-for="(msg,index) in messages"
        :key="index"
      >{{msg}}</p>
    </div>
    <div class="input-area">
      <a-input-search v-model="inputValue" placeholder="输入内容" @search="onSearch" size="large">
        <a-button slot="enterButton">发送</a-button>
      </a-input-search>
    </div>
  </div>
</template>

<script>
import hostIdMap from "../assets/host_id_map.json";
import nonsense from "../assets/nonsense.json";
import { setInterval } from "timers";
import nonsenseName from "../assets/nonsense_name.json"
export default {
  data() {
    return {
      messages: [`欢迎来到${hostIdMap[this.$route.params.id]}的直播间`],
      inputValue: "",
      showGif: true,
      randomColor: ""
    };
  },
  methods: {
    onSearch(text, e) {
      this.messages.push(
        `匿名用户010${Math.floor(Math.random() * 250)}说： ${text}`
      );
      this.inputValue = "";
    },
    addNonsense() {
      let nonsenseMsg = nonsense[Math.floor(Math.random() * 49) + 1];
      let nonsenseUser = nonsenseName[Math.floor(Math.random() * 20 + 1)]
      let randomColors = [
        "#ff4081",
        "#e040fb",
        "#ff1744",
        "#5e35b1",
        "#3949ab",
        "#1e88e5",
        "#0097a7",
        "#00796b",
        "#388e3c"
      ];
      let randomIndex = Math.floor(Math.random() * 7);
      this.randomColor = randomColors[randomIndex];
      this.messages.push(
        `${nonsenseUser}说： ${nonsenseMsg}`
      );
      if (this.messages.length > 100) {
        this.messages = [`欢迎来到${hostIdMap[this.$route.params.id]}的直播间`];
      }
    },
    doTheRandom() {
      let min = 0.5;
      let max = 5;
      let rand = Math.floor(Math.random() * (max - min + 1) + min);
      this.addNonsense()
      setTimeout(this.doTheRandom, rand * 1000);
    }
  },
  mounted: function() {
      this.doTheRandom()
  }
};
</script>

<style scoped>
.chat-area {
  width: 100%;
  height: 63vh;
  background: #f3f3f3;
  padding: 10px;
  border-radius: 5px;
}

.screen {
  background: white;
  width: 99%;
  margin: 0 auto;
  height: 50vh;
  padding: 10px 20px;
  overflow-y: scroll;
}

.input-area {
  padding: 10px;
}
</style>