<template>
  <a-menu mode="horizontal" :defaultSelectedKeys="['all']" class="menu">
    <a-menu-item 
    v-for="item in items" 
    @click="subNavBarItemClickHandler"
    :key="item.key"
    >
     {{item.text}}
    </a-menu-item>
  </a-menu>
</template>

<script>
export default {
  data() {
    return {
    };
  },
  props:[
    'items'
  ],
  methods: {
    subNavBarItemClickHandler(e) {
      this.$emit("navChange",e.key);
    }
  }
};
</script>

<style scoped>
.menu {
  border:unset;
  margin-left: 4em;
}
</style>